




















































	
	export default {
	  
		data() {
			return {
				showPopup: false,
				popType:'bottom',
        popState:0, //1,2,3
			};
		},
    methods: {
      goPage(e) {
        let path = e.currentTarget.dataset.path
        let page = e.currentTarget.dataset.page||''
        uni.navigateTo({
          url: path+'?page='+page
        })
      },
    	//展示居中 popup
    	showMiddlePopup() {
    		this.hidePopup();
    		this.popType = 'bottom';
    		this.showPopup = true;
    	},
    	//统一的关闭popup方法
    	hidePopup() {
    		this.showPopup = false;
    	  this.popState=1
    	},
      tixianHandle(state){
        this.popState = state+1
        if (this.popState==4) {
        	this.hidePopup()
        }
      }
    },
	}
